<script>
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
// import distributes from '@/views/kanban_bsb/distributes.vue';
// 导入所需接口
import {
  deviceRealLiveGetDeviceRealState,
  deviceRealLiveGetPageByDevice,
  getEnergyTodayUse,
  getEnergyTodayAndWeek
} from '@/api/kanban';
import {
  STATUS_CODE
  // TITLE
} from '@/utils/constantsFile';
// import {showErrorNotify} from '@/utils/notify';
// 导入通用方法
import {
  // getGroupId,
  getTodayFirstDay,
  getTodayLastDay
} from '@/utils/publicFunction2';

export default {
  name: 'kanban',
  components: {
    // 加载未审核组件
    // distributes
  },
  data() {
    return {
      title: '',
      groupId: 0,
      timer: {},
      timer4: {},
      time: '',
      warnHtml: '',
      devStateHtml: '',
      UtilizNum: 0,
      devList: [],
      devLbIndex: 0,
      warnList: [],
      // 设备分布数据
      productTaskData: [],
      // 生产任务 每行的展示的设备数
      rowDeviceNum: 10,
      repairCard: {
        no: 0,
        do: 0,
        off: 0
      },
      maintainCard: {
        no: 0,
        do: 0,
        off: 0
      },
      checkCard: {
        no: 0,
        do: 0,
        off: 0
      },
      current: 1,
      size: 6,
      current_w: 1,
      size_w: 4,
      kanbanConfig: { title: '数字化车间看板', key: '', time: 15000 },
      electricList: [],
      todayElecticeNum: 0
    };
  },
  mounted() {
    // 拿到表格挂载后的真实DOM
    const table = this.$refs.right1content;
    console.log(table.right1content);
    // 拿到表格中承载数据的div元素
    const divData = table;
    // // 拿到元素后，对元素进行定时增加距离顶部距离，实现滚动效果(此配置为每100毫秒移动1像素)
    this.finga(divData);

    if (this.kanbanConfig.showEnergy) {
      const table4 = this.$refs.right4content;
      const divData4 = table4;
      this.finga4(divData4);
    }
  },
  created() {
    var that = this;

    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }

    console.log(this.getUrlKeyMain('tvNo', window.location.href));
    if (this.getUrlKeyMain('tvNo', window.location.href) !== null) {
      this.title = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].title;
      this.groupId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].id;
    } else {
      this.title = that.kanbanConfig.title;
    }

    // this.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];

    // setInterval(() => {
    //   that.tick();
    // }, 1000);
    this.getDataTime();

    this.$nextTick(() => {
      this.showLineAndDev(); // 设备分布
      this.init();
    });

    setInterval(() => {
      this.showLineAndDev(); // 设备分布
    }, 5000);

    if (that.kanbanConfig) {
      setTimeout(() => {
        setInterval(() => {
          that.init();
        }, that.kanbanConfig.time);
      }, 0);
    } else {
      setTimeout(() => {
        setInterval(() => {
          that.init();
        }, 15000);
      }, 0);
    }
  },
  methods: {
    init() {
      this.showEcharts2Data(); // 今日设备状态分布
    },
    finga(divData) {
      this.timer = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    finga4(divData) {
      this.timer4 = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer4);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga4(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    async showElectric() {
      try {
        var condtion = {
          current: 1,
          size: 99999999
        };
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const res = await getEnergyTodayUse({ energyEnum: 'ELECTRICITY' }, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.electricList = res.rows;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
      try {
        // eslint-disable-next-line no-redeclare
        var condtion = {};
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const res = await getEnergyTodayAndWeek({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.todayElecticeNum = res.rows.electricityStatistics.dayEnergyConsumption;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEcharts2Data() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceRealLiveGetDeviceRealState({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.showEcharts2(
            [
              { name: '运行', value: res.rows.runCount },
              { name: '空闲', value: res.rows.idleCount },
              { name: '离线', value: res.rows.offLineCount },
              { name: '告警', value: res.rows.warnCount }
            ]
          );
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showLineAndDev() {
      var condtion = {
        conditionJson1: [],
        current: 1,
        size: 99999999
      };
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      condtion.conditionJson1.push({
        column: 'isProductionDevice',
        operator: 1,
        ptype: 'int',
        type: 'eq',
        value: 1
      });
      try {
        const res = await deviceRealLiveGetPageByDevice({
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay()
        }, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var groupArr = [];
          var devJSON = [];
          // var temp = {};
          var data = res.rows;
          for (var i = 0; i < data.length; i++) {
            var lineName = '';
            if (data[i].productionLine) {
              lineName = data[i].productionLine;
            } else if (data[i].workshop) {
              lineName = data[i].workshop;
            } else if (data[i].factory) {
              lineName = data[i].factory;
            } else {
              lineName = '其他';
            }
            var index = groupArr.indexOf(lineName);
            if (index === -1) {
              groupArr.push(lineName);

              devJSON.push({
                lineName: lineName,
                devListData: [data[i]]
              });
            } else {
              devJSON[index].devListData.push(data[i]);
            }
          }
          this.htmlDevState(devJSON);
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    htmlDevState(devJSON) {
      this.devStateHtml = '';

      var sbLine = 9;
      var cnum = 75;

      var canWeb = true;
      if (window.innerWidth <= 1024) {
        sbLine = 6;
        cnum = 24;
        canWeb = false;
      } else if (window.innerWidth <= 1280) {
        sbLine = 8;
        cnum = 32;
        canWeb = false;
      }
      const height = (this.$refs.right1content.offsetHeight * 0.3) - cnum;
      var arr = devJSON;
      var html = '';
      for (var i = 0; i < arr.length; i++) {
        // 标记设备需要遍历几次
        var listNum = Math.ceil(arr[i].devListData.length / sbLine);
        // 标记第几次遍历
        var flag = 0;
        // 标记设备遍历长度
        var arrlength = 0;

        if (i !== 0) {
          var num = Math.ceil(arr[i - 1].devListData.length / sbLine);

          if (num >= 2) {
            if (canWeb) {
              html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
            } else {
              html += '<div class="lineCON" style="margin-top:' + ((num - 1) * height) + 'px;" >';
            }
            // html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
          } else {
            html += '<div class="lineCON" >';
          }
        } else {
          html += '<div class="lineCON" >';
        }

        html += '      <p class="lineName" >' + arr[i].lineName + '</p>';

        for (var j = 0; j < listNum; j++) {
          // 若设备长度与预设长度一致
          if (arr[i].devList <= sbLine) {
            arrlength += arr[i].devListData.length;
          } else {
            // 若下一次轮播设备数已经短于预设值
            if ((flag + 1) * sbLine > arr[i].devListData.length) {
              arrlength += (arr[i].devListData.length % sbLine);
            } else {
              arrlength += sbLine;
            }
          }
          flag++;
          html += '      <div class="lineDev" >';
          html += '        <div class="blackDev" >';

          for (var w = 0; w < sbLine; w++) {
            html += '       <div class="blackTask" ></div>';
          }
          html += '           <div class="blackLine" ></div>';
          html += '           </div>';
          html += '           <div class="blackState" >';
          for (var x = j * sbLine; x < arrlength; x++) {
            html += '           <div class="devTask state' + arr[i].devListData[x].deviceRealState + '" >';
            if (arr[i].devListData[x].isProductionCount === 1) {
              if (arr[i].devListData[x].currentShiftName) {
                html += '          <span class="devTask_span2"  >' + arr[i].devListData[x].currentValue + '</span>';
              } else {
                html += '          <span class="devTask_span2"  >' + arr[i].devListData[x].todayValue + '</span>';
              }
            } else {
              html += '          <span class="devTask_span2"  ></span>';
            }
            html += '          <p>' + arr[i].devListData[x].deviceName + '</p>';
            html += '           </div>';
          }
          html += '          </div>';
          html += '      </div>';
        }
        html += '</div>';
      }
      this.devStateHtml = html;
      // $(".right1content").html(html);
      // setTimeout(() => {
      //   this.scrollDev();
      // }, 2500);
    },
    scrollDev() {
      var that = this;
      var newTop;
      // var up = this.$refs.right1content.scrollTop;
      // 使用定时器
      this.timer = setInterval(() => {
        this.$nextTick(() => {
          // console.log(this.$refs.right1content.scrollTop === newTop);
          // console.log(this.$refs.right1content.scrollTop, newTop);
          if (this.$refs.right1content.scrollTop === newTop) {
            // 清除定时器

            clearInterval(this.timer);
            setTimeout(() => {
              this.showLineAndDev();
            }, 2500);
          } else {
            // 每次在原来的基础上移动
            newTop = that.$refs.right1content.scrollTop;
            this.$refs.right1content.scrollTo(0, newTop + 1);
          }
        });
      }, 50);
    },
    showEcharts2(seriesData) {
      var fontSizJson = {
        a: 15,
        b: [5, 0, 5, 0],
        c: '1%',
        d: ['35%', '50%'],
        e: 15,
        f: 15
      };

      if (window.innerWidth <= 1024) {
        fontSizJson = {
          a: 10,
          b: [0, 0, 0, 0],
          c: '0%',
          d: ['30%', '50%'],
          e: 10,
          f: 10
        };
      } else if (window.innerWidth <= 1280) {
        fontSizJson = {
          a: 12,
          b: [2, 0, 2, 0],
          c: '0%',
          d: ['30%', '50%'],
          e: 12,
          f: 12
        };
      }
      const lineChart = this.$echarts.init(this.$refs.echarts2);
      var color = [
        '#09DBA0',
        '#E2CD16',
        '#A0A0A0',
        '#F1464E'
      ];

      var legend = [
        '运行',
        '空闲',
        '离线',
        '告警'
      ];
      // var seriesData = seriesData;
      const option = {
        color: color,
        grid: {
          top: '15%',
          left: 0,
          right: fontSizJson.c,
          bottom: 5,
          containLabel: true
        },
        legend: {
          orient: 'vertical',
          top: 'center',
          right: 0,
          itemWidth: fontSizJson.e,
          itemHeight: fontSizJson.f,
          textStyle: {
            color: '#ffffff',
            align: 'left',
            verticalAlign: 'middle',
            rich: {
              name: {
                color: 'rgba(255,255,255,1)',
                fontSize: fontSizJson.a,
                padding: fontSizJson.b
              },
              value: {
                color: 'rgba(255,255,255,0.5)',
                fontSize: fontSizJson.a
              },
              rate: {
                color: 'rgba(255,255,255,0.9)',
                fontSize: fontSizJson.a
              }
            }
          },
          data: legend,
          formatter: (name) => {
            if (seriesData.length) {
              const item = seriesData.filter((item) => item.name === name)[0];
              return `{name|${name}:}{rate| ${item.value}台}`;
            }
          }
        },
        series: [{
          name: '需求类型占比',
          color: ['rgba(75, 243, 249, 0.2)'],
          type: 'pie',
          center: fontSizJson.d,
          radius: ['45%', '75%'],
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '12'
              }
            }
          },
          labelLine: {
            show: false,
            length: 0,
            length2: 0
          },
          data: [
            { name: '全部', value: 100 }
          ]
        },
        {
          name: '需求类型占比',
          type: 'pie',
          center: fontSizJson.d,
          radius: ['45%', '68%'],
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '12'
              }
            }
          },
          labelLine: {
            show: false,
            length: 0,
            length2: 0
          },
          data: seriesData
        }]
      };
      lineChart.setOption(option, true);
      window.addEventListener('resize', () => {
        lineChart.resize();
      });
    },
    // 名称格式化
    nameFormat(value, cutter, newline) {
      var s = value;
      var num0 = cutter;
      var num1 = cutter;
      var flag = 1; // 换行标记
      if (s.length > 2 * cutter + 1) {
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter) {
            num0 = rs[0].length;
            num1 = 2 * cutter - rs[0].length;
          }
          if (rs[1].length < cutter) {
            num0 = 2 * cutter - rs[1].length;
            num1 = rs[1].length;
          }
          rs[0] = rs[0].substring(0, num0);
          rs[1] = rs[1].substring(0, num1);
          if (rs != null) {
            s = rs.join('|');
          }
        }
      } else {
        // eslint-disable-next-line no-redeclare
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter && rs[1].length < cutter) {
            flag = 0;
          }
          if (rs != null) {
            s = rs.join('|');
          }
        }
      }
      if (newline === 1) {
        if (flag === 1) {
          s = s.substring(0, s.length / 2) + '\n' + s.substring(s.length / 2);
        } else if (flag === 0) {
          s = s.split('|').join('\n');
        }
      }
      return s;
    },
    // 看板时间
    tick() {
      var objD = new Date();
      var yy = objD.getYear();
      if (yy < 1900) yy = yy + 1900;
      var MM = objD.getMonth() + 1;
      if (MM < 10) MM = '0' + MM;
      var dd = objD.getDate();
      if (dd < 10) dd = '0' + dd;
      var hh = objD.getHours();
      if (hh < 10) hh = '0' + hh;
      var mm = objD.getMinutes();
      if (mm < 10) mm = '0' + mm;
      var ss = objD.getSeconds();
      if (ss < 10) ss = '0' + ss;
      this.time = yy + ' 年 ' + MM + ' 月 ' + dd + ' 日 ' + hh + ' : ' + mm + ' : ' + ss;
    },
    // 看板时间
    getDataTime() {
      // $.ajax({
      //   type: 'GET',
      //   url: 'http://oa.junhepumps.com:8081/idevcloudwebsub48/junheProductionTask/getPageDeviceRealInformation.action?pageNum=1&pageSize=74',
      //   data: {},
      //   dataType: 'jsonp',
      //   success: function(data) {
      //     console.log('请求成功', data);
      //   },
      //   error: function(data) {
      //     console.log('请求失败', data);
      //   },
      //   crossDomain: true,
      //   xhrFields: { withCredentials: true }
      // });
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      console.log('时间', $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date'));
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    showLogo2() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_lt2';
      } else {
        return '';
      }
    },
    showLogo3() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_ry2';
      } else {
        return '';
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    formatDateThree(time) {
      var date = '';
      var D = '';
      date = new Date(time);
      var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      D = M + '-' + d;
      return D;
    },
    limitMoveNum() {
      let num = 0;
      this.productTaskData.forEach(item => {
        num += item.length;
      });
      return num > 2 ? 1 : 3;
    }
  }
};
</script>

<style lang="less" scoped>
/deep/ * {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 0px;
